export default {
  model: {
    prop: "page",
    event: "change"
  },
  props: ["pageNum", "page"],

  data() {
    return {
      pageSize: "",
      pageList: [], // 用于遍历跳转的页面列表
      after: false,
      before: false
    };
  },

  mounted() {
    this.pageSize = this.page;
  },
  methods: {
    changePage(page) {
      this.$emit("change", page);
      // this.$emit('change', page);
      this.pageSize = page;
    },
    // 上一个
    goPrior() {
      if (this.pageSize === 1) return;
      this.changePage(this.pageSize - 1);
    },

    // 下一个
    goNext() {
      if (this.pageSize === this.pageNum) return;
      this.changePage(this.pageSize + 1);
    },
    setList(newPage) {
      if (newPage == 1) {
        let arr = [];
        for (let i = 1; i <= this.pageNum; i++) {
          arr.push(i);
          if (arr.length == 5) break;
        }
        this.pageList = arr;
        this.before = false;
        this.after = this.pageNum > 5;
      } else {
        let arr = [];
        let first = this.pageSize <= 2 ? 1 : this.pageSize - 2;
        while (true) {
          if (first >= 1 && first <= this.pageNum) {
            arr.push(first);
          }

          if (first === this.pageNum) {
            if (arr.length < 5) {
              while (true) {
                if (arr.length === 5 || arr[0] === 1) break;
                arr.unshift(this.pageNum - arr.length);
              }
            }
            break;
          }

          if (arr.length === 5) break;

          first++;
        }

        this.after = arr[arr.length - 1] < this.pageNum;
        this.before = arr[0] > 1;
        this.pageList = arr;
      }
    }
  },

  watch: {
    //   监听页码修改, 重构页码条
    pageSize(newPage) {
      this.setList(newPage);
    },
    pageNum() {
      this.setList(this.pageSize);
    }
  }
};
