import { frontlists, info } from "~/api/base";
import pager from "~/components/paging";
import loading from "~/components/loading";
export default {
  head() {
    return {
      title: this.pageTitle || "新闻中心"
    };
  },
  components: {
    pager,
    loading
  },
  data() {
    return {
      pageTitle: "新闻中心-蜂乐购优选官网-区域会员制社交电商平台",
      showNews: false, // 是否显示富文本内容
      content: {}, // 富文本内容
      text: "",
      page: 1,
      limit: 5,
      newsList: [],
      total: "",
      page_num: "",
      showItem: "", // 当前哪个新闻列表高亮
      loading: false,
      id: "",
      after_status: 0,
      before_status: 0,
      after_main_title: "", // 下一个标题
      before_main_title: "", // 上一个标题
      before_id: "",
      after_id: ""
    };
  },
  mounted() {
    if (this.$route.query.id) this.id = this.$route.query.id;
    this.getList();
    this.handleNews("");
    if (process.client) {
      window.onscroll = this.onPageScroll;
    }
  },
  destroyed() {
    if (process.client) {
      window.onscroll = this.onPageScroll;
    }
  },
  methods: {
    onPageScroll() {
      if (this.loading || !this.mobileStatus) return;
      //为了保证兼容性，这里取两个值，哪个有值取哪一个
      //scrollTop就是触发滚轮事件时滚轮的高度

      if (this.throttleKey) {
        return;
      }
      this.throttleKey = true;

      setTimeout(() => {
        this.throttleKey = false;
        clearTimeout(this.throttleKey);
      }, 50);
      let scrollTop =
        (document.documentElement.scrollTop || document.body.scrollTop) +
        document.documentElement.clientHeight;
      let pageHeight = document.documentElement.scrollHeight;

      /**保证是向下滑动 */
      if (pageHeight - 50 <= scrollTop && scrollTop > this.scrollTop) {
        this.getList();
      }
      this.scrollTop = scrollTop;
    },
    // 获取广告列表数据
    getList() {
      // if (this.throttleKey2) {
      //   return;
      // }
      // this.throttleKey2 = true;

      // setTimeout(() => {
      //   this.throttleKey2 = false;
      //   clearTimeout(this.throttleKe2);
      // }, 1500);

      this.loading = true;
      frontlists({
        page: this.page,
        limit: this.limit
      }).then(res => {
        if (res.data.code === 1) {
          this.page =
            res.data.data.list.length != 0 ? this.page + 1 : this.page;
          if (this.mobileStatus) {
            this.newsList = this.newsList.concat(res.data.data.list);
          } else {
            this.newsList = res.data.data.list;
          }
          this.page_num = res.data.data.page_num;
        }
        this.$nextTick(() => {
          this.loading = false;
        });
      });
    },
    handleNewsInfo(id) {
      this.$router.push("/news?id=" + id);
      this.id = id;
    },
    handleNews(type) {
      if (this.$route.query.id) {
        info({
          id:
            type == "after"
              ? this.after_id
              : type == "before"
              ? this.before_id
              : this.id,
          type
        }).then(res => {
          if (res.data.code === 1) {
            this.content = res.data.data.list[0];
            this.pageTitle = res.data.data.list[0].main_title;
            this.text = this.content.content;
            this.id = res.data.data.list[0].id;
            this.after_status = res.data.data.after_status;
            this.before_status = res.data.data.before_status;
            this.after_main_title = res.data.data.after_main_title;
            this.before_main_title = res.data.data.before_main_title;
            this.before_id = res.data.data.before_id;
            this.after_id = res.data.data.after_id;
            this.showNews = true;
          } else {
            console.warn(res.data.msg);
          }
          if (process.client) {
            this.$nextTick(() => {
              document.body.scrollTop = document.documentElement.scrollTop = 0;
            });
          }
        });
      } else {
        if ((this.showNews = true)) {
          this.showNews = false;
          this.pageTitle = "新闻中心-蜂乐购优选官网-区域会员制社交电商平台";
        }
      }
    }
  },

  filters: {
    /**
     * 传入一个2020-03-17 10:00:00 格式 提取中的 天
     */
    handleDay: function(time) {
      let day = new Date(time).getDate();
      return day < 10 ? "0" + day : day;
    },
    /**
     * 传入一个2020-03-17 10:00:00 格式 提取中的 年月
     */
    handleDate: function(time) {
      let date = new Date(time);
      let month = date.getMonth() + 1;
      let year = date.getFullYear();
      return year + "." + (month < 10 ? "0" + month : month);
    }
  },
  computed: {
    // 判断当前是否为移动端布局
    mobileStatus() {
      return this.$store.state.isMobile;
    }
  },
  watch: {
    page(old, n) {},
    $route() {
      this.handleNews("");
    }
  }
};
